import axios from 'axios';

const url = 'http://192.168.1.243/api/devices';

class DeviceService {
    static getDevices() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                // If no devices, convert message to an empty array to prevent errors
                resolve( Array.isArray(data) ? data.map(device => ({ ...device })) : [] );
            } catch (err) {
                reject(err);
            }
        });
    }

    static createDevice(newDevice) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, newDevice);
                resolve(res.status);
            } catch (err) {
                reject(err);
            }
        })
    }

    static deleteDevice(id) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.delete(url + '/' + id);
                const data = res.data;
                resolve(data.message);
            } catch (err) {
                reject(err);
            }
        })
    }

    static sendCommand(deviceId, action) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url + '/command', {
                    id: deviceId,
                    command: action
                });
                const data = res.data;
                resolve (data);
            } catch (err) {
                reject(err);
            }
        });
    }
}

export default DeviceService;