          v-on:deviceData="showModal"
        ></SwitchedComponent>
      </vs-row>
    </b-container>

    <b-modal
      centered
      size="lg"
      hide-footer
      hide-header
      v-model="show"
      style="backgroundColor: #cccccc; opacity:0.95"
    >
      <b-container fluid>
        <div style="textAlign: left; color: #330033">
          <span style="fontSize: 2.0em; marginRight:3.0vh">
            <strong>{{ modalDevice.name }}</strong>
          </span>
          <span style="fontSize: 1.25em">
            <b-badge
              pill
              style="backgroundColor: #330033; color: white; marginRight:1vh"
            >{{ modalDevice.room.name }}</b-badge>
            <b-badge
              pill
              style="backgroundColor: #330033; color: white; marginRight:1vh"
            >{{ modalDevice.category.name }}</b-badge>
            <vs-button
              @click="deleteConfirm(modalDevice._id)"
              size="small"
              color="danger"
              type="border"
            >Delete Device</vs-button>
            <v-icon name="pencil" scale="1.5" color="#330033" style="marginLeft:3vh"></v-icon>
          </span>
          <div style="fontSize: 1.025em">
            <i>{{ modalDevice.description }}</i>
          </div>
          <hr>
          <b-row>
            <b-col sm="4">
              <i>Power:</i>
              <span style="fontSize: 1.2em">
                <strong>{{ modalDevice.switch.state ? 'On' : 'Off'}}</strong>
              </span>
              <span v-if="modalDevice.switch.type == 'BulbRGB'">
                ,
                <i>Level:</i>
                <span style="fontSize: 1.2em">
                  <strong>{{ modalDevice.switch.brightness }}%</strong>
                </span>
              </span>
            </b-col>
            <b-col sm="8" style="textAlign: right">
              <b-badge
                v-on:click="sendCommand(modalDevice._id, 'togglePower')"
                style="fontSize: 1.15em; border: 3px solid #330033; backgroundColor: white; color: #330033;"
              >Power</b-badge>
              <b-badge
                v-if="modalDevice.switch.type == 'BulbRGB'"
                v-on:click="sendCommand(modalDevice._id, 'pairDevice')"
                style="fontSize: 1.15em; border: 3px solid #330033; backgroundColor: white; color: #330033; marginLeft:2.5vh"
              >Pairing</b-badge>
            </b-col>
          </b-row>
          <hr>
          <b-row v-if="modalDevice.switch.type == 'BulbRGB'">
            <b-col sm="5" v-if="modalDevice.switch.type == 'BulbRGB'">
              <b-badge
                pill
                style="backgroundColor:white; border: 1.5px solid #330033; color:#330033;
                        fontSize: 1.125em; marginBottom: 2.5vh"
              >Level</b-badge>
              <br>
              <span v-bind:style="brightnessButton" v-on:click="decBrightness(modalDevice._id)">
                <v-icon name="minus-circle" scale="2.75" color="#330033"></v-icon>
              </span>
              <span v-bind:style="brightnessButton" v-on:click="incBrightness(modalDevice._id)">
                <v-icon name="plus-circle" scale="2.75" color="#330033"></v-icon>
              </span>
            </b-col>
            <b-col sm="7" v-if="modalDevice.switch.type == 'BulbRGB'">
              <b-row>
                <b-col sm="12">
                  <b-badge
                    @click="setWhite(modalDevice._id)"
                    style="fontSize: 1.0em; border: 3px solid #330033; backgroundColor: white; color: #330033;"
                  >Set White</b-badge>
                </b-col>
                <b-col sm="12">
                  <div style="width: 100%; textAlign: center">
                    <v-icon
                      name="square"
                      scale=".5"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color - 72) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale=".75"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color - 56) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale="1"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color - 40) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale="1.5"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color - 24) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale="1.85"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color - 8) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale="2.75"
                      :style="{ color: 'hsl(' + modalDevice.switch.color + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale="1.85"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color + 8) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale="1.5"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color + 24) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale="1"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color + 40) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale=".75"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color + 56) + ', 100%,50%)'}"
                    ></v-icon>
                    <v-icon
                      name="square"
                      scale=".5"
                      :style="{ color: 'hsl(' + (modalDevice.switch.color + 72) + ', 100%,50%)'}"
                    ></v-icon>
                  </div>
                </b-col>
                <b-col sm="12">
                  <b-row>
                    <b-col sm="5">
                      <div style="width: 100%; textAlign: center">
                        <span
                          v-bind:style="brightnessButton"
                          v-on:click="decColor(modalDevice._id)"
                        >
                          <v-icon name="arrow-left" scale="2.5" color="#330033"></v-icon>
                        </span>
                      </div>
                    </b-col>
                    <b-col sm="2" style="textAlign: center">
                      <div style="width: 100%; textAlign: center">
                        <b-badge
                          pill
                          style="fontSize: 0.65em; border: 1.5px solid #330033; backgroundColor: white; color: #330033;"
                        >{{ modalDevice.switch.color }}/359</b-badge>
                      </div>
                    </b-col>
                    <b-col sm="5">
                      <div style="width: 100%; textAlign: center">
                        <span
                          v-bind:style="brightnessButton"
                          v-on:click="incColor(modalDevice._id)"
                        >
                          <v-icon name="arrow-right" scale="2.5" color="#330033"></v-icon>
                        </span>
                      </div>
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>
            </b-col>
          </b-row>
          <hr>
        </div>
        <b-btn
          class="mt-3"
          block
          @click="hideModal"
          style="backgroundColor: #330033; color: white;"
        >Done</b-btn>
      </b-container>
    </b-modal>
  </div>
</template>

<script>
import SwitchedComponent from "./SwitchedComponent";
import SceneComponent from "./SceneComponent";
import TimeDateComponent from "./TimeDateComponent";
import WeatherComponent from "./WeatherComponent";
import DeviceStatusComponent from "./DeviceStatusComponent";
import DeviceService from "../DeviceService";
import io from "socket.io-client";
//import Vue from "vue";
import moment from "moment";
import Swiper from "swiper";

export default {
  name: "DeviceListComponent",
  components: {
    TimeDateComponent,
    WeatherComponent,
    DeviceStatusComponent,
    SwitchedComponent,
    SceneComponent,
    Swiper
  },
  data() {
    return {
      dateTime: [],
      devices: [],
      scenes: [],
      weatherData: [],
      modalDevice: {
        _id: "",
        name: "",
        category: {
          name: ""
        },
        room: {
          name: ""
        },
        switch: {
          brightness: 0,
          color: 0,
          state: false
        },
        color: { r: 255, g: 0, b: 0 }
      },
      show: false,
      deviceStatus: { total: 0, types: {} },
      error: "",
      message: "",
      deviceContainerStyle: {
        paddingLeft: "20px",
        paddingRight: "20px"
      },
      buttonStyle: {
        padding: "10px",
        margin: "5px"
      },
      brightnessButton: {
        width: "75px",
        paddingTop: "5px",
        paddingBottom: "5px",
        marginRight: "2.75vh"
      },
      mySwiper: new Swiper(".swiper-container", {
        // Optional parameters
        slidesPerView: 3,
        spaceBetween: 30,
        slidesPerGroup: 3,
        loop: true,
        loopFillGroupWithBlank: true
      }),
      colorPickerStyle: "hsl(this.modalDevice.switch.color, 100%, 50%)",
      socket: io("192.168.1.243:80")
    };
  },
  async created() {
    try {
      await this.refreshDeviceData();
      this.updateClock();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async sendCommand(id, cmd) {
      try {
        var res = await DeviceService.sendCommand(id, cmd);
      } catch (err) {
        this.error = err.message;
      }
    },
    deleteConfirm() {
      this.show = false;
      this.$vs.dialog({
        type: "confirm",
        color: "danger",
        title: "Delete Device?",
        text: "Are You Sure You Want To Delete This Device?",
        accept: this.deleteDevice,
        cancel: this.reshowModal
      });
    },
    async deleteDevice() {
      try {
        var message = await DeviceService.deleteDevice(this.modalDevice._id);
        this.$vs.dialog({
          color: "primary",
          title: "Success",
          text: message
        });
        this.refreshDeviceData();
      } catch (err) {
        this.error = err.message;
      }
    },
    showModal(device) {
      this.modalDevice = device;
      this.show = true;
    },
    reshowModal() {
      this.show = true;
    },
    hideModal() {
      this.show = false;
    },
    async refreshDeviceData() {
      var tempList = await DeviceService.getDevices();
      this.devices = [];
      this.scenes = [];

      var active = {
        total: 0,
        types: {}
      };
      tempList.forEach(device => {
        if (device.switch.state == true) {
          if (device.switch.type !== "Scene") {
            active.total++;
          }
          if (Object.keys(active.types).includes(device.category.name)) {
            active.types[device.category.name]++;
          } else {
            active.types[device.category.name] = 1;
          }
        }
        if (device.switch.type === "Scene") {
          this.scenes.push(device);
        } else {
          this.devices.push(device);
        }
      });
      this.deviceStatus = active;
    },
    updateDeviceStates(data) {
      console.log("updateDeviceStates() called...");
      var active = {
        total: 0,
        types: {}
      };
      data.forEach(device => {
        this.devices.forEach(dev => {
          if (dev._id === device._id) {
            console.log("MATCH, BEFORE: " + JSON.stringify(dev.switch));
            dev.switch.state = device.switch.state;
            if (dev.switch.brightness !== null)
              dev.switch.brightness = device.switch.brightness;
            if (dev.switch.color !== null)
              dev.switch.color = device.switch.color;
            if (dev.switch.isWhite !== null)
              dev.switch.isWhite = device.switch.isWhite;
            console.log("MATCH, BEFORE: " + JSON.stringify(dev.switch));
          }
        });
        if (this.modalDevice._id === device._id) {
          this.modalDevice.switch.state = device.switch.state;
          if (this.modalDevice.switch.brightness !== null)
            this.modalDevice.switch.brightness = device.switch.brightness;
          if (this.modalDevice.switch.color !== null)
            dev.switch.color = device.switch.color;
          if (this.modalDevice.switch.isWhite !== null) {
            this.modalDevice.switch.isWhite = device.switch.isWhite;
          }
        }
        if (device.switch.state == true) {
          if (device.switch.type !== "Scene") {
            active.total++;
          }
          if (Object.keys(active.types).includes(device.category.name)) {
            active.types[device.category.name]++;
          } else {
            active.types[device.category.name] = 1;
          }
        }
      });
      this.deviceStatus = active;
    },
    decBrightness(id) {
      DeviceService.sendCommand(id, "decrementBrightness");
    },
    incBrightness(id) {
      DeviceService.sendCommand(id, "incrementBrightness");
    },
    decColor(id) {
      DeviceService.sendCommand(id, "decrementColorVal");
    },
    incColor(id) {
      DeviceService.sendCommand(id, "incrementColorVal");
    },
    setWhite(id) {
      DeviceService.sendCommand(id, "whiteLight");
    },
    updateClock() {
      var now = new moment().format("dddd#MMMM Do#h:mma");
      this.dateTime = now.split("#");
      setTimeout(this.updateClock, 5000);
    },
    onUpdateColor(id, val) {
      alert("id: " + id + " val: " + val);
    }
  },
  mounted() {
    this.socket.on("DEVICE_STATES", data => {
      this.updateDeviceStates(data);
    });
    this.socket.on("WEATHER", data => {
      this.weatherData = data;
    });
  }
};
</script>

