import axios from 'axios';

const url = 'http://192.168.1.243/api/devices/rooms';

class RoomService {
    static getRooms() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                // If no rooms, convert message to an empty array to prevent errors
                resolve(Array.isArray(data) ? data.map(room => ({ ...room
                })) : []);
            } catch (err) {
                reject(err);
            }
        });
    }

    static createRoom() {

    }

    static deleteRoom(id) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.delete(url + '/' + id);
                const data = res.data;
                resolve(data.message);
            } catch (err) {
                reject(err);
            }
        })
    }

    static sendCommand(deviceId, action) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url + '/command', {
                    id: deviceId,
                    command: action
                });
                const data = res.data;
                resolve(data);
            } catch (err) {
                reject(err);
            }
        });
    }
}

export default RoomService;