<template>
   <vs-col type="flex" vs-justify="center" vs-align="center" vs-xs="6" vs-sm="3" vs-lg="2">
    <vs-card
      class="cardx"
      style="width:98%; border: 2.25px solid #330033; backgroundColor: #2897d7; height: 11.5vh; padding: 0; opacity: 0.70"
    >
      <div v-on:click="sendCommand(device._id, 'togglePower')">
        <div style="marginBottom:0vh; height: 2.65vh">
          <vs-row vs-type="flex" vs-justify="flex-start">
            <p style="fontSize:1.15em; color: #4d004d">
              <strong>Scene:</strong>
            </p>
          </vs-row>
        </div>
      </div>
      <div>
        <vs-row vs-justify="flex-end">
          <vs-col vs-w="12">
            <div>
              <div
                v-on:click="sendCommand(device._id, 'togglePower')"
                style="fontSize: 1.5em; color: #FFFFFF"
              >
                <strong>{{ device.name }}</strong>
              </div>
            </div>
          </vs-col>
        </vs-row>
      </div>
    </vs-card>
  </vs-col>
</template>

<script>
import DeviceService from "../DeviceService";
import Icon from "vue-awesome/components/Icon";

export default {
  name: "SwitchedComponent",
  components: {
    "v-icon": Icon
  },
  props: ["deviceData"],
  data() {
    return {
      device: this.deviceData,
      error: "",
      show: false,
      disableSwitch: false,
      deviceStyle: {
        backgroundColor: "darkBlue",
        color: "white",
        outline: "1px solid",
        outlineColor: "darkBlue",
        paddingTop: "10px",
        marginBottom: "25px"
      },
      actionBoxStyle: {
        paddingTop: "10px",
        paddingBottom: "15px",
        backgroundColor: "white"
      },
      buttonStyle: {
        padding: "15px",
        margin: "5px",
        backgroundColor: "black",
        color: "white",
        fontWeight: "bold"
      },
      isOff: {
        opacity: "0.7"
      }
    };
  },
  methods: {
    async sendCommand(id, cmd) {
      try {
        var device = await DeviceService.sendCommand(id, cmd);
        this.device.switch.state = device.switch.state;
      } catch (err) {
        this.error = err.message;
      }
    },
    isRgbColorMode(isWhite, type) {
      return !isWhite && type === "BulbRGB";
    }
  }
};
</script>