import axios from 'axios';

const url = 'http://192.168.1.243/api/devices/categories';

class DeviceCategoryService {
    static getCategories() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                // If no categories, convert message to an empty array to prevent errors
                resolve( Array.isArray(data) ? data.map(category => ({ ...category })) : [] );
            } catch (err) {
                reject(err);
            }
        });
    }

    static createCategory() {

    }

    static deleteCategory(id) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.delete(url + '/' + id);
                const data = res.data;
                resolve(data.message);
            } catch (err) {
                reject(err);
            }
        })
    }

    static sendCommand(deviceId, action) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url + '/command', {
                    id: deviceId,
                    command: action
                });
                const data = res.data;
                resolve (data);
            } catch (err) {
                reject(err);
            }
        });
    }
}

export default DeviceCategoryService;