<template>
  <vs-col type="flex" vs-justify="center" vs-align="center" vs-xs="6" vs-sm="3" vs-lg="2">
    <vs-card
      class="cardx"
      v-bind:style="{ opacity: device.switch.state ? 0.85 : 0.3 }"
      style="width:98%; border: 2.25px solid #330033; height: 30vh"
    >
      <div v-on:click="sendCommand(device._id, 'togglePower')">
        <div
          style="paddingBottom:0vh; marginBottom:0vh; borderBottom:1.5px solid #330033; height: 5vh"
        >
          <vs-row vs-type="flex" vs-justify="flex-start">
            <p style="fontSize:1.25em; color: #330033">
              <strong>{{ device.name }}</strong>
            </p>
          </vs-row>
        </div>
        <div
          style="height: 14vh; borderBottom:1px solid #330033; marginBottom:0.5vh; padding:0.25vh"
        >
          <b-badge
            pill
            style="backgroundColor:#330033; marginRight:0.25vh; marginBotom:0.5vh; fontSize: .9em"
          >
            <i>{{ device.category.name }}</i>
          </b-badge>
          <span v-if="device.switch.type == 'BulbRGB'">
            <b-badge
              pill
              style="backgroundColor: #330033; fontSize: .9em; marginRight:0.25vh; marginBottom:0.5vh"
            >
              <i>Level:</i>
              <strong>{{ device.switch.brightness }}%</strong>
            </b-badge>
          </span>
          <span v-if="isRgbColorMode(device.switch.isWhite, device.switch.type)">
            <b-badge
              pill
              style="backgroundColor: white; border: 1.25px solid #330033; color:#330033; fontSize: .9em"
            >Hue:
              <v-icon
                name="palette"
                scale=".8"
                :style="{ color: 'hsl(' + (device.switch.color + 8) + ', 100%,50%)'}"
              ></v-icon>
            </b-badge>
          </span>
          <span v-if="!isRgbColorMode(device.switch.isWhite, device.switch.type)">
            <b-badge
              pill
              style="backgroundColor: white; border: 1.25px solid #330033; color:#330033; fontSize: .9em"
            >Hue: White</b-badge>
          </span>
        </div>
      </div>
      <div>
        <vs-row vs-justify="flex-end">
          <vs-col vs-w="10">
            <div
              v-on:click="sendCommand(device._id, 'togglePower')"
              style="width:100%; height: 5vh"
            ></div>
          </vs-col>
          <vs-col vs-w="2">
            <vs-button color="primary" type="gradient" v-on:click="$emit('deviceData', device)">
              <v-icon name="bars"></v-icon>
            </vs-button>
          </vs-col>
        </vs-row>
      </div>
    </vs-card>
  </vs-col>
</template>

<script>
import DeviceService from "../DeviceService";
import Icon from "vue-awesome/components/Icon";

export default {
  name: "SwitchedComponent",
  components: {
    "v-icon": Icon
  },
  props: ["deviceData"],
  data() {
    return {
      device: this.deviceData,
      error: "",
      show: false,
      disableSwitch: false,
      deviceStyle: {
        backgroundColor: "darkBlue",
        color: "white",
        outline: "1px solid",
        outlineColor: "darkBlue",
        paddingTop: "10px",
        marginBottom: "25px"
      },
      actionBoxStyle: {
        paddingTop: "10px",
        paddingBottom: "15px",
        backgroundColor: "white"
      },
      buttonStyle: {
        padding: "15px",
        margin: "5px",
        backgroundColor: "black",
        color: "white",
        fontWeight: "bold"
      },
      isOff: {
        opacity: "0.7"
      }
    };
  },
  methods: {
    async sendCommand(id, cmd) {
      try {
        await DeviceService.sendCommand(id, cmd);
      } catch (err) {
        this.error = err.message;
      }
    },
    isRgbColorMode(isWhite, type) {
      return !isWhite && type === "BulbRGB";
    }
  }
};
</script>