<template>
    <div>
        <header>
            <div>
                <b-nav pills>
                    <b-nav-item><router-link style="color: white" to="/">Devices</router-link></b-nav-item>
                    <b-nav-item active variant="secondary"><router-link style="color: white" to="/devices/new">Create</router-link></b-nav-item>
                </b-nav>
            </div>
        </header>
        <b-container fluid>
            <b-row>
                <b-col sm="10" offset-sm="1" style="backgroundColor: white; height: 87vh; marginTop:2vh; paddingTop:2.5vh">
                    <b-row style="marginBottom: 1.5vh">
                        <b-col sm="3" offset-sm="1"><strong><label>Name:</label></strong></b-col>
                        <b-col sm="7"><b-form-input v-model="deviceName" type="text"></b-form-input></b-col>
                    </b-row>
                    <b-row style="marginBottom: 0vh">
                        <b-col sm="3" offset-sm="1"><strong><label>Description:</label></strong></b-col>
                        <b-col sm="7"><b-form-input v-model="deviceDescription" type="text"></b-form-input></b-col>
                    </b-row>
                    <hr />
                    <b-row style="marginBottom: 0.5vh">
                        <b-col sm="6">
                            <span style="textAlign: left; fontSize: 1.2em">Device Config</span>
                        </b-col>
                        <b-col sm="6">
                            <span style="textAlign: center; fontSize: 1.2em"><strong>Summary</strong></span>   
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col sm="3" >
                            <div style="marginBottom:0.25vh">
                                <b-form-select style="fontSize: 0.85em" v-model="roomSelected" :options="roomOptions" class="mb-3" />
                            </div>
                            <div style="marginBottom:0.25vh">
                                <b-form-select style="fontSize: 0.85em" v-model="categorySelected" :options="categoryOptions" class="mb-3" />
                            </div>
                            <div style="marginBottom:0vh">
                                <b-form-select style="fontSize: 0.85em" v-model="deviceSelected" :options="deviceOptions" class="mb-3" />
                            </div>
                        </b-col>
                        <b-col sm="3">
                            <div v-if="deviceSelected == 'RFOutlet'">
                                <b-row style="marginBottom: 4vh; marginTop: 4.5vh">
                                    <b-col sm="4" style="textAlign: right; fontSize:0.85em"><label>On Code:</label></b-col>
                                    <b-col sm="8"><b-form-input style="fontSize: 0.85em" v-model="onCode" type="number"></b-form-input></b-col>
                                </b-row>
                                <b-row>
                                    <b-col sm="4" style="textAlign: right; fontSize:0.85em"><label>Off Code:</label></b-col>
                                    <b-col sm="8"><b-form-input style="fontSize: 0.85em" v-model="offCode" type="number"></b-form-input></b-col>
                                </b-row>
                            </div>
                            <div v-if="deviceSelected == 'BulbRGB'">
                                <b-row style="marginBottom: 2vh">
                                    <b-col sm="4" style="textAlign: right; fontSize:0.85em"><label>Style:</label></b-col>
                                    <b-col sm="8">
                                        <b-form-select style="fontSize: 0.85em" v-model="bulbStyle" :options="bulbStyles" class="mb-3"/>
                                    </b-col>
                                </b-row>
                                <b-row>
                                    <b-col sm="4" style="textAlign: right; fontSize: 0.85em"><label>Group:</label></b-col>
                                    <b-col sm="8"><b-form-input v-model="deviceGroup" type="number"></b-form-input></b-col>
                                </b-row>
                            </div>
                        </b-col>
                        <b-col sm="6" >
                            <div style="fontSize: 1.05em"><b>Device Name: </b>{{ deviceName }}</div>
                            <div style="fontSize: 1.05em"><b>Description: </b>{{ deviceDescription }}</div>
                            <div style="fontSize: 1.05em"><b>Room: </b>{{ roomSelected.display }}</div>
                            <div style="fontSize: 1.05em"><b>Device Category: </b>{{ categorySelected.display }}</div>
                            <div style="fontSize: 1.05em"><b>Device Type: </b>{{ deviceSelected }}</div>
                            <hr />
                            <b-button v-on:click="createDevice()" style="fontSize:0.875em">Create Device</b-button>
                        </b-col>
                    </b-row>
                    <b-row style="marginTop: 0vh; marginLeft:2.5">
                        <b-col sm-12 style="textAlign: left">
                            <div v-if="readerActive" style="fontSize:0.5em">
                                <b-button style="marginRight:15px; fontSize: 0.75em" v-on:click="disableCodeReader()">Disable RF Scanner</b-button>
                                <span v-if="rfCode">Last RF Code: </span><strong>{{ rfCode ? rfCode : 'Ready...' }}</strong>
                            </div>
                            <div v-if="!readerActive">
                                <b-button style="marginRight:15px; fontSize: 0.75em" v-if="!readerActive" v-on:click="activateCodeReader()">
                                    Enable RF Scanner
                                </b-button>
                                <i><span style="fontSize: 0.65em">Activate RF Scanner To Get On/Off Codes...</span></i>
                            </div>
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>  
        </b-container>

        <b-modal
      centered
      size="lg"
      hide-footer
      hide-header
      v-model="show"
      style="backgroundColor: #cccccc; opacity:0.95"
    >
      <b-container fluid>
        <div style="textAlign: left; color: #330033">
          <span style="fontSize: 2.0em; marginRight:3.0vh">
            <strong>{{ modalDevice.name }}</strong> Was Created.
          </span>
          <span style="fontSize: 1.25em">
            <b-badge
              pill
              style="backgroundColor: #330033; color: white; marginRight:1vh"
            >{{ modalDevice.room.name }}</b-badge>
            <b-badge
              pill
              style="backgroundColor: #330033; color: white; marginRight:1vh"
            >{{ modalDevice.category.name }}</b-badge>
            <v-icon name="trash" scale="1.5" backgroundColor="#330033" style="marginLeft:3vh"></v-icon>
            <v-icon name="pencil" scale="1.5" color="#330033" style="marginLeft:3vh"></v-icon>
          </span>
          <div style="fontSize: 1.025em">
            <i>{{ modalDevice.description }}</i>
          </div>
          <hr>
          <i>Power:   </i>
          <span style="fontSize: 1.25em">
            <strong>{{ modalDevice.switch.state ? 'On' : 'Off'}}</strong>
          </span>
          <span v-if="modalDevice.switch.type == 'BulbRGB'">
            /
            <span><i>Level:   </i></span>
            <span style="fontSize: 1.15em">
              <strong>{{ modalDevice.switch.brightness }}%</strong>
            </span>
            <hr>
          </span>
          <b-row>
            <b-col sm="3" v-if="modalDevice.switch.type == 'BulbRGB'">
              <b-badge
                pill
                style="backgroundColor:white; border: 1.5px solid #330033; color:#330033;
                        fontSize: 1.25em"
              >Level</b-badge>
            </b-col>
            <b-col sm="3" v-if="modalDevice.switch.type == 'BulbRGB'">
              <span v-bind:style="brightnessButton" v-on:click="decBrightness(modalDevice._id)">
                <v-icon name="minus-circle" scale="2.5" color="#330033"></v-icon>
              </span>
              <span v-bind:style="brightnessButton" v-on:click="incBrightness(modalDevice._id)">
                <v-icon name="plus-circle" scale="2.5" color="#330033"></v-icon>
              </span>
            </b-col>
            <b-col sm="3">
                <b-badge v-if="modalDevice.switch.type == 'BulbRGB'" 
                pill v-on:click="sendCommand(modalDevice._id, 'pairDevice')" 
                style="fontSize: 1.3em; backgroundColor: #330033; color: white; marginBottom: 1.75vh">
                    Send Pairing Signal
                </b-badge>
                <b-badge pill v-on:click="sendCommand(modalDevice._id, 'togglePower')" 
                style="fontSize: 1.3em; backgroundColor: #330033; color: white;">
                    Toggle Power
                </b-badge>
            </b-col>
          </b-row>
          <hr>
        </div>
        <b-btn
          class="mt-3"
          block
          @click="hideModal"
          style="backgroundColor: #330033; color: white;"
        >Done</b-btn>
      </b-container>
    </b-modal>
    </div>
</template>

<script>
import DeviceService from "../DeviceService";
import RoomService from "../RoomService";
import DeviceCategoryService from "../DeviceCategoryService";
import io from 'socket.io-client';

export default {
  name: "NewDeviceComponent",
  components: {},
  data() {
    return {
      rooms: [],
      show: false,
      modalDevice: {
        name: "",
        category: {
          name: ""
        },
        room: {
          name: ""
        },
        switch: {
          brightness: 0,
          state: false
        }
      },
      categories: [],
      deviceType: "",
      deviceName: "",
      deviceDescription: "",
      roomSelected: {'display' : null, 'val' : null},
      categorySelected: {'display' : null, 'val' : null},
      deviceSelected: "",
      onCode: null,
      offCode: null,
      bulbStyle: null,
      deviceGroup: null,
      deviceId: null,
      roomOptions: [
        { value: {'display': null, 'val': null}, text: 'Room:' }
      ],
      categoryOptions: [
        { value: {'display': null, 'val': null}, text: 'Category:' }
      ],
      deviceOptions: [
          { value: "", text: 'Device Type:' },
          { value: 'RFOutlet', text: 'RF-Controlled Outlet' },
          { value: 'BulbRGB', text: 'RGB Milight Bulb/Strip' }
      ],
      bulbStyles: [
          { value: 'RGBW', text: 'RGBW' },
          { value: 'RGB', text: 'RGB' },
          { value: 'CCT', text: 'CCT' },
          { value: 'RGBCCT', text: 'RGBCCT' }
      ],
      rfCode: '',
      readerActive: false,
      error: "",
      message: "",
      deviceContainerStyle: {
        paddingLeft: "20px",
        paddingRight: "20px"
      },
      buttonStyle: {
        padding: "10px",
        margin: "5px"
      },
      socket: io('192.168.1.243:80')
    };
  },
  async created() {
    try {
      this.devices = await DeviceService.getDevices();
      this.rooms = await RoomService.getRooms();
      this.categories = await DeviceCategoryService.getCategories();
      this.disableCodeReader();

      // Populate drop-down selectors, convert data to correct format.
      var roomOps = this.roomOptions;
      var categoryOps = this.categoryOptions;
      this.rooms.forEach(function (room) {
         console.log(room.name);
         console.log(room._id);
         roomOps.push({value: {display: room.name, val: room._id}, text: room.name});
      });
      this.categories.forEach(function (category) {
         console.log(category.name);
         console.log(category._id);
         categoryOps.push({value: {display: category.name, val: category._id}, text: category.name});
      });
      // Set resulting arrays
      this.roomOptions = roomOps;
      this.categoryOptions = categoryOps;
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async sendCommand(id, cmd) {
      try {
        await DeviceService.sendCommand(id, cmd);
      } catch (err) {
        this.error = err.message;
      }
    },
    hideModal() {
      this.show = false;
      this.$router.push({name: "Home"});
    },
    async deleteCategory(id) {
      try {
        this.message = await DeviceCategoryService.deleteCategory(id);
        this.categories = await DeviceCategoryService.getCategories();
      } catch (err) {
        this.error = err.message;
      }
    },
    async deleteRoom(id) {
      try {
        this.message = await RoomService.deleteRoom(id);
        this.rooms = await RoomService.getRooms();
      } catch (err) {
        this.error = err.message;
      }
    },
    async createDevice() {
      try {
        var newDevice = {
            name: this.deviceName,
            description: this.deviceDescription,
            room: this.roomSelected.val,
            category: this.categorySelected.val,
            switch: {
                type: this.deviceSelected,
            }
        };

        if (this.deviceSelected == 'RFOutlet') {
            newDevice.switch.code = {
                on: this.onCode,
                off: this.offCode
            };
        }
        else if (this.deviceSelected == 'BulbRGB') {
            if (this.bulbStyle) newDevice.switch.bulbStyle = this.bulbStyle;
            if (this.deviceGroup) newDevice.switch.deviceGroup = this.deviceGroup;
            if (this.deviceId) newDevice.switch.deviceId = this.deviceId;
        }

        var postResponse = await DeviceService.createDevice(newDevice);
        if (postResponse == 200) {
            this.modalDevice = newDevice;
            this.show = true;
        }
      } catch (err) {
        this.error = err.message;
      }
    },
    sendMessage(boolVal) {
        this.socket.emit('GET_RF_CODE', {
            message: boolVal
        });
    },
    activateCodeReader() {
        this.readerActive = true;
        this.sendMessage(true);
    },
    disableCodeReader() {
        this.readerActive = false;
        this.sendMessage(false);
        this.rfCode = "";
    }
  },
  mounted() {
    this.socket.on('MESSAGE', (data) => {
        this.message = data;
    });
    this.socket.on('RF_CODE', (data) => {
        this.rfCode = data.code;
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
