<template>
  <vs-col type="flex" vs-justify="center" vs-align="center" vs-xs="6" vs-sm="3" vs-lg="2">
    <b-card
      no-header
      no-footer
      no-body
      style="backgroundColor: #8585ad; opacity:0.8; height:30.0vh; width: 98%; padding:0vh; marginBottom:3vh;
        box-shadow: 0 10px 28px 0 rgba(0, 0, 0, .4), 0 20px 56px 0 rgba(0, 0, 0, 0.1); border:2px solid #330033"
    >
      <div>
        <b-card-body
          style="textAlign: center; color:#330033; padding: 0.0vh;
                marginBottom:0vh; marginTop:1.5vh"
        >
          <b-badge
            pill
            style="backgroundColor:#330033; fontSize:1.3em; marginBottom:1.95vh"
          >{{ dateTime[0] }}</b-badge>
          <b-badge
            pill
            style="fontSize:1.2em; backgroundColor: #330033; marginBottom:1.95vh;"
          >{{ dateTime[2] }}</b-badge>
          <b-badge
            pill
            style="fontSize:0.9em; backgroundColor:white; border: 2.0px solid #330033; color:#330033"
          >{{ dateTime[1] }}</b-badge>
        </b-card-body>
      </div>
    </b-card>
  </vs-col>
</template>

<script>
import moment from "moment";

export default {
  name: "TimeDateComponent",
  components: {},
  data() {
    return {
      dateTime: []
    };
  },
  created() {
    try {
      this.updateClock();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    updateClock() {
      var now = new moment().format("dddd#MMMM Do#h:mma");
      this.dateTime = now.split("#");
      setTimeout(this.updateClock, 5000);
    }
  }
};
</script>